































import { AppMutations } from "@/store/app/mutations";
import Vue from "vue";

export default Vue.extend({
  name: "Header",
  computed: {
    isNavBarOpen: {
      get(): boolean {
        return this.$store.getters.isNavBarOpen;
      },
      set(value: boolean): void {
        this.$store.commit(AppMutations.SET_IS_NAV_BAR_OPEN, value);
      },
    },
  },
});
